import React,{useState} from 'react';

const AddCommentForm = ({ articleName, setArticleInfo }) => {
    const [user, setUser] = useState("")
    const [comment, setComment] = useState("")

    const addComment = async () => {
        const result = await fetch(`/api/articles/${articleName}/comment`, {
            method:'post',
            body: JSON.stringify({user, comment}),
            headers: {
                'Content-Type': 'application/json'
            }
        });
        const body = await result.json();
        setArticleInfo(body);
        setUser('');
        setComment('');
    }

    return (
        <div className="add-comment-form">
            <h3>Add a Comment</h3>
            <label>
                Name: 
                <input type="test" value={user} onChange={(event) => setUser(event.target.value)} />
            </label>
            <label>
                Comment:
                <textarea rows="4" cols="50" value={comment} onChange={(event) => setComment(event.target.value)}/>
            </label>
            <button onClick={() => addComment()}>Add Comment</button>
        </div>
    );
}

export default AddCommentForm;
